*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: currentColor; }

ul,
ol,
li {
  list-style: none; }

html,
body {
  font-family: PingFang SC,Helvetica Neue,Hiragino Sans GB,WenQuanYi Micro Hei,Microsoft Yahei,sans-serif;
  line-height: normal; }

body {
  background-color: red; }
